import Vue from 'vue'
import Vuex from 'vuex'
import auth from "solid-auth-client";
import solidFileClient from "solid-file-client";
import axios from 'axios';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loggedIn: false,
    user: ""
  },
  mutations: {
    login(state, { user }) {
      state.loggedIn = true;
      state.user = user;
    },
    logout(state) {
      state.loggedIn = false;
    }
  },
  actions: {
    async popupLogin({ dispatch, commit }) {
      let session = await auth.currentSession();
      let popupUri = "https://solid.community/common/popup.html";
      if (!session) session = await auth.popupLogin({ popupUri });
      dispatch("initializeUser", {
        webId: session.webId,
        fc: solidFileClient(auth)
      });
      commit("login", {
        user: session.webId
      });
    },
    async initializeUser({commit},{ webId, fc }) {
      axios.post("http://localhost:3000/api/registerUser", {
        userIRI: webId
      }).then(res => {
        console.log(res);
      }).catch(err => {
        console.log(err);
      });
      fc.createFolder("https://serkanozel.solid.community/folder")
      .then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log(err);
      });
    },
    async checkLogin({ commit, dispatch }) {
      auth.trackSession(session => {
        if (!session) {
          dispatch("popupLogin");
        }
        else {
          dispatch("initializeUser", {
            webId: session.webId
          });
          commit("login", { user: session.webId });
        }
      });
    },
    logoutAction({ commit }) {
      auth.logout().then(() => {
        commit("logout");
      });
    }
  },
  modules: {
  }
})
